<template>
  <div id="app">

    <el-container>
      <el-header>
        <Header
          :activeIndex="activeIndex"/>
      </el-header>
      <el-container id="body" class="loading">
        <el-main>
          <router-view></router-view>
        </el-main>
        <el-aside>

        </el-aside>
      </el-container>
      <el-footer>

      </el-footer>
    </el-container>
  </div>
</template>

<script>
import Header from "./components/Header";

export default {
  components: {Header},
  name: 'App',
  data() {
    return {
      activeIndex: '',
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}


::-webkit-scrollbar {
  width: 0 !important;
}

::-webkit-scrollbar {
  width: 0 !important;
  height: 0;
}



/*!*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*!
::-webkit-scrollbar {
  width: 8px;
  height: 16px;
  background-color: #F5F5F5;
}

!*定义滚动条轨道 内阴影+圆角*!
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #F5F5F5;
}

!*定义滑块 内阴影+圆角*!
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
  background-color: #555;
}*/
</style>



