import Vue from 'vue'
import Router from 'vue-router'
import Main from "../pages/Main";
import Show from "../components/Show";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: resolve => require(['../pages/Main'], resolve),
      redirect: 'index',
      children: [
        {
          path: 'index',
          name: 'main',
          component: Main
        }
      ]
    },
    {
      path: '/show',
      name: 'show',
      component: Show
    }
  ]
})



