<template>
  <div>
    <div id="center_d">
      <H1 v-text="data.title"></H1>
      <H3 v-text="data.text"></H3>
    </div>
  </div>
</template>

<script>
export default {
  name: "Show",
  data() {
    return {
      data: []
    }
  },
  created() {
    this.data = this.$route.params.data
  }
}
</script>

<style scoped>

#center_d {
  position: relative;
  margin: 0 auto;
  justify-content: center;
}
</style>



